@import url('./main.css');

ul {
    list-style: none;
    padding-left: 0;
}

li {
    color: white;
    display: inline-block;
    width: 20%;
    text-align: center;
}

li:hover {
    text-decoration: underline;
}

.main-menu-dropdown {
    text-align: center;
    position: absolute;
    width: 12%;
}

.main-menu {
    display: block;
    color: white;
    padding: 10px 0px;
    background-color: var(--primary-color);
}

.offset {
    display: block;
    background-color: none;
    height: 30px;
    width: 100%;
}

.sub-menu {
    display: block;
    background-color: var(--primary-color);
    padding: 10px;
}

#hide {
    display: none;
}

#show {
    display: block;
}