@import url('./main.css');

.main {
    margin-top: 0px;
}

.header {
    width: 80%;
    margin: auto;
    margin-top: 100px;
    display: grid;
    grid-template-columns: repeat(8, 1fr);
    grid-gap: 30px;
    grid-auto-rows: minmax(50px, auto);
}

.nav {
    grid-column: 3/9;
    grid-row: 1;
    align-self: center;
}

.footer {
    color: var(--primary-color);
    display: block;
    text-align: center;
    width: 100%;
    margin-bottom: 100px;
    margin-top: 100px;
}

.footer-logo {
    width: 200px !important;
    margin: auto;
}

.logo {
    grid-row: 1;
    grid-column: 1/3;
}

.sticker {
    position: fixed;
    width: 120px;
    right: 80px;
    bottom: 50px;
}

@media (max-width: 768px) {
    body {
        color: red;
    }

    .header {
        width: 80%;
        margin: auto;
        display: block;
    }

    .logo {
        width: 40%;
        margin: auto;
        margin-top: 2em;
    }

    .nav {
        width: 100%;
        margin-top: 2em;
    }

    .sticker {
        width: 20%;
        right: 5px;
    }
}