:root {
    --primary-color: #6eb697;
    --secondary-color: #dd9e20;
    --decorative-color: rgba(110, 182, 151, 0.6);
}

.wrapper {
    width: 80%;
    margin: auto;
    margin-top: 100px;
    display: grid;
    grid-template-columns: repeat(8, 1fr);
    grid-gap: 30px;
    grid-auto-rows: minmax(50px, auto);
}

img {
    width: 100%;
    display: block;
    object-fit: cover;
}

.title-area {
    grid-column: 1/3;
    color: var(--primary-color);
    text-align: center;
}

.content-area {
    grid-row: auto;
    grid-column: 3/9;
    font-size: 25px;
    text-align: center;
}

@media (max-width: 768px) {

    .wrapper {
        width: 80%;
        margin: auto;
        margin-top: 2em;
        display: block;
    }
}